import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./TitanNews.module.scss"
import PageContentSeparator from "../components/PageContentSeparator"
import ParagraphSeparator from "../components/ParagraphSeparator"
import titanNewsImage from "../images/titanNews.png"
import titanNewsFontsImage from "../images/titanNewsFonts.png"

export default () => {
  const { mockupTitanNews } = useStaticQuery(graphql`
    query {
      mockupTitanNews: file(relativePath: { eq: "mockupTitanNews.png" }) {
        childImageSharp {
          fixed(height: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.pageHeadingSection}>
        <Img
          fixed={mockupTitanNews.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
          className={styles.pageHeadingImage}
        />
      </div>
      <div className={styles.pageMainContentSection}>
        <div className={styles.pageMainContent}>
          <PageContentSeparator />
          <div>
            <div className={styles.projectTitle}>TitaNews</div>
            <div className={styles.projectSubTitle}>Newspaper Website</div>
          </div>
          <ParagraphSeparator />
          <div className={styles.projectText}>
            <div>
              Designing the homepage of an online daily newspaper is such a big
              challenge for alignment and arrangement because of a huge amount
              of content.
            </div>
            <ParagraphSeparator />
            <div>
              The content is arranged clean and clear as much as possible for
              users to follow the latest and hottest news on the top, as well as
              related content by categories below. There is also an Offer
              section for advertisement and Gallery to watch videos and photos.
            </div>
            <ParagraphSeparator />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Main color:</div>
              <div>
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox1}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox2}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox3}`}
                />
              </div>
            </div>
            <ParagraphSeparator />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Fonts:</div>
              <img src={titanNewsFontsImage} alt="" />
            </div>
            <ParagraphSeparator />
            <div>Full page:</div>
            <ParagraphSeparator />
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        <img src={titanNewsImage} className={styles.projectImage} alt="" />
      </div>
      <PageContentSeparator />
    </React.Fragment>
  )
}
